import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee, faPlus } from '@fortawesome/free-solid-svg-icons'
import './Product.css';
const Product = (props) => {

const {img, name, salary, email, phone, website}= props.product;

  return (
    <div className="product">
      <div>
<img src={img} alt=""/>
      </div>
      <div>
      <h2 className="product-name">{name}</h2>
      <p>Phone: {phone}</p>
      <p>Email: {email}</p>
      <p>Website: {website}</p>
      <h3>Salary: ${salary}</h3>
      

      
  <button 
  className="main-btn" 
  onClick={() => props.handleAddProduct(props.product)}
  ><FontAwesomeIcon icon={faPlus} /> Add Friends</button>
      </div>
       </div>
  );
};

export default Product;