import React from 'react';
import logo from '../../images/android-chrome-192x192.png';
import './Header.css';
const Header = () => {
  return (
    <div className="header">
       <img src={logo} alt=""/>
       <nav>
         <a href="/friends">Friends</a>
         <a href="/profile">Profile</a>
         <a href="/manage">Manage</a>
       </nav>
    </div>
  );
};

export default Header;